.Button_button__33CwW {
  cursor: pointer;
  font: inherit;
  font-weight: bold;
  border: none;

  color: white;

  background: rgb(255, 83, 124);
  background: linear-gradient(
    153deg,
    rgba(255, 83, 124, 0.9) 0%,
    rgba(255, 183, 197, 0.9) 100%
  );
  padding: 0.35rem 1rem;

  display: flex;
  justify-content: space-around;
  align-items: center;
  border-radius: 25px;

  /* font-weight: bold; */
  box-shadow: 2px 3px 6px 2px rgba(0, 0, 0, 1);
}

.video-background iframe,
.video-foreground {
  pointer-events: none;
}

.video-background iframe,
.video-foreground {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.video-background {
  background: #000;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -99;
}

/* TODO: The responsivity breaks on extremes, like */
/* ultra wide and minimum height or ultra thin and minimum width */
/* also fullscreen is broken */
@media (min-aspect-ratio: 16/9) {
  .video-foreground {
    height: 300%;
    top: -100%;
  }
}
@media (max-aspect-ratio: 16/9) {
  .video-foreground {
    width: 300%;
    left: -100%;
  }
}
@media (max-width: 600px) {
  .video-foreground {
    width: 350%;
    left: -125%;
  }
}
@media (max-height: 400px) {
  .video-foreground {
    height: 500%;
    top: -200%;
  }
}
.audio {
  display: none;
}

